import Vue from 'vue'

Vue.directive('clickOnce', {
  inserted(el, binding) {
    el.addEventListener('click', () => {
      if (!el.disabled) {
        // const button = el.__vue__ //  Vue 
        // if (!button.loading) {
        //   button.loading = true //  loading 
        // }

        el.classList.add('is-disabled')
        el.disabled = true
        let clearStatus = function () {
          el.disabled = false
          el.classList.remove('is-disabled')
          // button.loading = false //  loading 
        }

        if (
          binding.value &&
          binding.value === 'function' &&
          binding.value() &&
          typeof binding.value().then === 'function'
        ) {
          const result = binding.value()
          result
            .then(() => {
              clearStatus()
            })
            .finally(() => {
              clearStatus()
            })
        } else {
          setTimeout(() => {
            clearStatus()
          }, 1000)
        }
      }
    })
  },
})

Vue.directive('auth', {
  inserted(el, binding, vnode) {
    //  rbacList 
    const vm = vnode.context //  Vue 
    const checkPermission = () => {
      const res = vm.$store.state.rbacList
      // 
      if (!res.includes(binding.value)) {
        el.remove()
      }
    }

    // 
    if (vm.$store.state.rbacList && vm.$store.state.rbacList.length) {
      checkPermission()
    } else {
      //  rbacList 
      const unwatch = vm.$watch(
        () => vm.$store.state.rbacList,
        (newVal) => {
          if (newVal && newVal.length) {
            checkPermission()
            unwatch() // 
          }
        }
      )
    }
  },
})
