<template>
  <div class="content">
    <el-row :gutter="20">
      <el-col :span="5">
        <h3></h3>
        <div>
          <el-button type="primary" @click="trggerShow" size="mini"
            ></el-button
          >
        </div>
        <div class="group-div">
          <el-tree
            node-key="groupId"
            :data="treeList"
            @node-click="handleNodeClick"
          >
            <div style="width: 100%" slot-scope="{ data }">
              <span>
                <i class="el-icon-office-building" /> {{ data.groupName }}</span
              >
              <div style="float: right">
                <el-dropdown @command="handleCommand($event, data)" size="mini">
                  <span>...</span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="delete"></el-dropdown-item>
                    <el-dropdown-item command="edit"></el-dropdown-item>
                    <el-dropdown-item command="subgroup"
                      ></el-dropdown-item
                    >
                  </el-dropdown-menu>
                </el-dropdown>
              </div>
            </div>
          </el-tree>
        </div>
      </el-col>

      <el-col :span="19">
        <el-empty v-if="!chooseGroup" description=""></el-empty>
        <div v-else>
          <h3>
            <i class="el-icon-office-building" />:{{ chooseGroup.groupName }}
          </h3>
          <el-tabs type="card" v-model="chooseName" @tab-click="clickTab">
            <el-tab-pane label="" name="user">
              <el-button type="primary" size="mini" @click="clickUser"
                ></el-button
              >
              <el-table :data="userTable" size="mini" height="400px">
                <el-table-column prop="userId" label="Id">
                </el-table-column>
                <el-table-column prop="userName" label="">
                </el-table-column>
                <el-table-column prop="gmtCreated" label="">
                  <template slot-scope="scope">
                    {{ scope.row.createTime | dateFormat }}
                  </template>
                </el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button type="text" @click="editUser(scope.row)"
                      ></el-button
                    >
                    <el-button type="text" @click="deleteUser(scope.row)"
                      ></el-button
                    >
                    <el-button type="text" @click="bindRole(scope.row)"
                      ></el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @size-change="handleUserSizeChange"
                @current-change="handleUserPageChange"
                :current-page="groupUserPage"
                :page-sizes="[10, 20, 50]"
                :page-size="groupUserSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="groupUserTotal"
              >
              </el-pagination>
            </el-tab-pane>
            <el-tab-pane label="" name="role">
              <div>
                <el-button type="primary" @click="groupBind" size="mini"
                  ></el-button
                >
              </div>
              <div>
                <el-table :data="groupRoles" size="mini" height="400px">
                  <el-table-column prop="roleId" label="ID">
                  </el-table-column>
                  <el-table-column prop="roleName" label="">
                  </el-table-column>
                  <el-table-column prop="description" label="">
                    <template slot-scope="scope">
                      <textView :text="scope.row.description" />
                    </template>
                  </el-table-column>
                  <el-table-column prop="createTime" label="">
                    <template slot-scope="scope">
                      {{ scope.row.createTime | dateFormat }}
                    </template>
                  </el-table-column>
                </el-table>
                <el-pagination
                  @size-change="handleUserSizeChange"
                  @current-change="handleUserPageChange"
                  :current-page="groupUserPage"
                  :page-sizes="[10, 20, 50]"
                  :page-size="groupUserSize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="groupUserTotal"
                >
                </el-pagination>
              </div>
            </el-tab-pane>
            <el-tab-pane label="">
              <el-collapse
                v-model="activeRoleNames"
                @change="loadRoleResources"
                accordion
              >
                <el-collapse-item
                  v-for="(item, index) in groupRoles"
                  :key="index"
                  :title="item.roleName"
                  :name="item.roleId"
                >
                  <h4>:</h4>
                  <el-checkbox-group v-model="checkList">
                    <el-checkbox
                      v-for="(api, num) in item.resources"
                      :key="num"
                      :label="api.resourceName"
                      :checked="true"
                      disabled
                    ></el-checkbox>
                  </el-checkbox-group>
                </el-collapse-item>
              </el-collapse>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>

    <el-dialog :visible.sync="showDialog" title="" @close="groupClose">
      <el-form v-model="groupForm" label-width="80px" size="mini">
        <el-form-item v-if="groupForm.parentId" label="">
          {{ groupForm.parentName }}
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="groupForm.groupName"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            :autosize="{ minRows: 3, maxRows: 4 }"
            placeholder=""
            v-model="groupForm.description"
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitGroup"></el-button>
          <el-button type="info" @click="groupClose"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog :visible.sync="showUserDialog" :title="userTitle">
      <el-form v-model="userForm" label-width="80px" size="mini">
        <el-form-item label="">
          <el-input v-model="userForm.userName" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="userForm.nickName" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="userForm.password"
            placeholder=""
            show-password
          />
        </el-form-item>

        <el-form-item label="" v-if="edistUser">
          <el-button
            type="primary"
            icon="el-icon-refresh"
            @click="resetPassword"
            ></el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitUser"></el-button>
          <el-button type="info" @click="cancelUser"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title=""
      :visible.sync="showBindDialog"
      width="60%"
      @close="cancelBind"
    >
      <el-form v-model="bindForm" size="mini" label-width="120px">
        <el-form-item :label="bindForm.userType == 2 ? 'ID' : 'ID'">
          <el-input
            v-model="bindForm.relationId"
            placeholder=""
            disabled
          />
        </el-form-item>
        <el-form-item :label="bindForm.userType == 2 ? '' : ''">
          <el-input v-model="bindForm.name" placeholder="" disabled />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="bindForm.userType" placeholder="" disabled>
            <el-option label="" :value="1"> </el-option>
            <el-option label="" :value="2"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="roleIds"
            multiple
            clearable
            placeholder=""
          >
            <el-option
              v-for="item in roleList"
              :key="item.roleId"
              :label="item.roleName"
              :value="item.roleId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitBind"></el-button>
          <el-button type="info" @click="cancelBind"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import userApi from '../../http/User'
import groupApi from '../../http/Group'
import roleApi from '../../http/Role'
import resourceApi from '../../http/Resource'
import textView from '../../components/text-view.vue'
export default {
  components: {
    textView,
  },
  data() {
    return {
      treeList: [],
      title: '',
      showDialog: false,
      groupForm: {},
      userTable: [],
      showUserDialog: false,
      userForm: {},
      chooseGroup: null,
      showBindDialog: false,
      bindForm: {},
      checkList: [],
      resourceList: [],
      roleList: [],
      chooseName: 'user',
      groupRoles: [],
      edistUser: false,
      userTitle: '',
      editGroup: false,
      groupUserPage: 1,
      groupUserSize: 10,
      groupUserTotal: 0,
      roleIds: [],
      activeRoleNames: [],
    }
  },
  methods: {
    resetPassword() {
      userApi.resetPassword({ userId: this.userForm.userId }).then((res) => {
        if (res.data) {
          this.$notify.success('')
        } else {
          this.$notify.error('')
        }
      })
    },
    groupClose() {
      this.groupForm = {}
      this.showDialog = false
    },
    handleUserSizeChange(size) {
      this.groupUserSize = size
      this.getGroupUsers()
    },
    handleUserPageChange(page) {
      this.groupUserPage = page
      this.getGroupUsers()
    },
    handleCommand(command, data) {
      if (command == 'delete') {
        groupApi.deleteGroup(data.groupId).then((res) => {
          if (res) {
            this.$notify.success('!')
            this.getGroups()
          }
        })
      }
      if (command == 'edit') {
        this.showDialog = true
        this.editGroup = true
        this.groupForm = data
      }
      if (command == 'subgroup') {
        this.groupForm.parentId = data.groupId
        this.groupForm.parentName = data.groupName
        this.showDialog = true
        this.editGroup = false
      }
    },
    groupBind() {
      this.bindForm = {
        relationId: this.chooseGroup.groupId,
        name: this.chooseGroup.label,
        userType: 2,
      }
      this.getAllRoles()
      this.showBindDialog = true
      this.getUserOrGroupRoles(this.chooseGroup.groupId)
    },
    getAllRoles() {
      roleApi.getAllRoles().then((res) => {
        this.roleList = res.data
      })
    },
    submitBind() {
      this.bindForm.roleIds = this.roleIds
      roleApi.bindRole(this.bindForm).then((res) => {
        if (res) {
          this.$notify.success('')
          this.getUserOrGroupRoles(this.chooseGroup.groupId, 2)
          this.cancelBind()
        }
      })
    },
    cancelBind() {
      this.showBindDialog = false
      this.bindForm = {}
      this.roleList = []
    },
    editUser(row) {
      this.showUserDialog = true
      this.userForm = row
      this.edistUser = true
      this.userTitle = ''
    },
    bindRole(row) {
      this.getAllRoles()
      this.showBindDialog = true
      this.getUserOrGroupRoles(row.userId, 1)
      this.bindForm = {
        userId: row.userId,
        relationId: row.userId,
        name: row.userName,
        userType: 1,
      }
    },
    clickUser() {
      this.showUserDialog = true
      this.userForm = {}
      this.edistUser = false
      this.userTitle = ''
    },
    handleNodeClick(data) {
      this.chooseGroup = data
      this.getGroupUsers()
      this.getUserOrGroupRoles(this.chooseGroup.groupId)
    },
    trggerShow() {
      this.showDialog = true
    },
    submitUser() {
      let data = this.userForm
      data.groupId = this.chooseGroup.groupId
      if (this.edistUser) {
        userApi.updateUser(this.userForm).then((res) => {
          if (res) {
            this.$notify.success('')
            this.showUserDialog = false
            this.getGroupUsers()
          }
        })
        return
      }
      userApi.createUser(this.userForm).then((res) => {
        if (res) {
          this.$notify.success('')
          this.showUserDialog = false
          this.getGroupUsers()
        }
      })
    },

    deleteUser(row) {
      userApi.deleteUser(row.userId).then((res) => {
        if (res) {
          this.$notify.success('')
          this.getGroupUsers()
        }
      })
    },
    cancelUser() {
      this.showUserDialog = false
      this.userForm = {}
    },
    submitGroup() {
      if (this.editGroup) {
        groupApi
          .updateGroup(this.groupForm.groupId, this.groupForm)
          .then((res) => {
            if (res.data) {
              this.$notify.success('')
              this.showDialog = false
              this.getGroups()
            } else {
              this.$notify.error('')
            }
          })
        return
      }
      groupApi.createGroup(this.groupForm).then((res) => {
        if (res) {
          this.$notify.success('')
          this.showDialog = false
          this.getGroups()
        } else {
          this.$notify.error('')
        }
      })
    },
    cancelGroup() {
      this.editGroup = false
      this.showDialog = false
      this.groupForm = {}
    },
    getGroups() {
      groupApi.getGroups().then((res) => {
        this.treeList = res.data
      })
    },
    clickTab(tab) {
      this.activeRoleNames = []
      if (tab.name == 'user') {
        this.getGroupUsers()
      }

      if (tab.name == 'role') {
        this.getUserOrGroupRoles(this.chooseGroup.groupId, 2)
      }
    },
    getUserOrGroupRoles(userId) {
      roleApi.getGropRoles(userId).then((res) => {
        this.groupRoles = res.data.data
        this.roleIds = []
        if (!res.data.data) {
          return
        }
        res.data.data.forEach((e) => {
          this.roleIds.push(e.roleId)
        })
      })
    },
    loadRoleResources(roleId) {
      resourceApi.getRoleResources(roleId).then((res) => {
        let array = []
        this.groupRoles.forEach((e) => {
          if (e.roleId == roleId) {
            e.resources = res.data
          }
          array.push(e)
        })

        this.groupRoles = array
      })
    },
    getGroupUsers() {
      groupApi
        .getGroupUsers(
          this.chooseGroup.groupId,
          this.groupUserPage,
          this.groupUserSize
        )
        .then((res) => {
          this.userTable = res.data.data
          this.groupUserTotal = res.data.total
        })
    },
  },
  created() {
    this.getGroups()
  },
}
</script>
<style lang="less" scoped>
.content {
  margin: 20px;
}
.group-div {
  margin-top: 20px;
}
.content-right {
  margin-right: 10px;
}
</style>
