<template>
  <div>
    <el-alert
      class="tips-info"
      title=""
      type="info"
      :closable="false"
      description=""
      show-icon
    >
    </el-alert>
    <div class="content">
      <el-tabs tab-position="left" v-model="configType" @tab-click="clickTab">
        <el-tab-pane label="Git" name="git">
          <el-divider content-position="left">Git</el-divider>
          <el-form :model="systemForm" size="mini" label-width="120px">
            <el-form-item label="" prop="gitType">
              <el-radio-group v-model="systemForm.gitType" @change="changeGit">
                <el-radio label="Gitea">Gitea</el-radio>
                <el-radio label="Gitlab">Gitlab</el-radio>
                <el-radio label="GitHub">GitHub</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="Git" prop="gitDomain">
              <el-input
                type="text"
                v-model="systemForm.gitDomain"
                placeholder="git,: https://gitlab.cn  IP+ "
              />
            </el-form-item>
            <el-form-item
              label=""
              prop="owner"
              v-if="systemForm.gitType != 'Gitlab'"
            >
              <el-input
                type="text"
                v-model="systemForm.owner"
                placeholder="git"
              />
            </el-form-item>
            <el-form-item label="token" prop="accessToken">
              <el-input
                type="text"
                v-model="systemForm.accessToken"
                placeholder="gittoken"
                show-password
              />
            </el-form-item>

            <el-divider content-position="left">Git</el-divider>
            <el-form-item>
              <template slot="label">
                Secret
                <el-tooltip
                  class="item"
                  effect="dark"
                  content=" Git  Webhook  secret "
                  placement="top-end"
                >
                  <i class="el-icon-info" />
                </el-tooltip>
              </template>
              <el-row :gutter="20">
                <el-col :span="10">
                  <el-input
                    type="text"
                    v-model="systemForm.pushSecret"
                    :show-password="showSecret"
                  />
                </el-col>
                <el-col :span="4">
                  <el-popover
                    placement="top"
                    width="300"
                    v-model="confirmRefresh"
                  >
                    <p style="margin: 0">
                       Secret  Git
                      Webhook<br />
                      
                    </p>
                    <div style="text-align: right; margin: 0">
                      <el-button
                        size="mini"
                        type="text"
                        @click="confirmRefresh = !confirmRefresh"
                        ></el-button
                      >
                      <el-button
                        type="primary"
                        size="mini"
                        @click="refreshSecret"
                        ></el-button
                      >
                    </div>
                    <el-button
                      slot="reference"
                      type="danger"
                      plain
                      icon="el-icon-refresh"
                      size="mini"
                      ></el-button
                    >
                  </el-popover>
                </el-col>
              </el-row>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="submitGit"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="repo">
          <el-form :model="repoForm" size="mini" label-width="120px">
            <el-form-item label="" prop="repositoryUrl">
              <el-input
                type="text"
                v-model="repoForm.repositoryUrl"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label="" prop="userName">
              <el-input
                type="text"
                v-model="repoForm.userName"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input
                type="text"
                v-model="repoForm.password"
                placeholder=""
                show-password
              />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitImage"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="Maven" name="maven">
          <el-form :model="mavenForm" size="mini" label-width="120px">
            <h5>Maven</h5>
            <el-form-item label="" prop="mavenUrl">
              <el-input
                type="text"
                v-model="mavenForm.mavenUrl"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label="" prop="userName">
              <el-input
                type="text"
                v-model="mavenForm.userName"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input
                type="text"
                v-model="mavenForm.password"
                placeholder=""
                show-password
              />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitMaven"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="build">
          <div class="verion-content">
            <el-button
              size="mini"
              icon="el-icon-plus"
              type="primary"
              @click="startAddVersion"
              ></el-button
            >
            <el-table
              :data="
                versionData.filter(
                  (data) =>
                    !searchVersion ||
                    data.name
                      .toLowerCase()
                      .includes(searchVersion.toLowerCase())
                )
              "
              height="500"
              size="mini"
            >
              <el-table-column label="" prop="name"> </el-table-column>
              <el-table-column
                label=""
                prop="type"
                :filters="[
                  { text: 'Java', value: 'Java' },
                  { text: 'Go', value: 'Go' },
                ]"
                :filter-method="filterLanguageTag"
                filter-placement="bottom-end"
              >
              </el-table-column>

              <el-table-column label="" prop="installPath">
              </el-table-column>
              <el-table-column label="" prop="description">
              </el-table-column>
              <el-table-column align="right">
                <template slot="header" slot-scope="scope">
                  <el-input
                    v-model="searchVersion"
                    clearable
                    size="mini"
                    @change="
                      () => {
                        scope.row
                      }
                    "
                    placeholder=""
                  />
                </template>
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.row)"
                    ></el-button
                  >
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row)"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="version-box">
      Windy, -: {{ consoleVersion }}
    </div>
    <el-dialog
      title=""
      :visible.sync="showVersionDialog"
      @close="cancelVersion"
    >
      <el-form
        :model="versionForm"
        ref="versionForm"
        label-width="120px"
        :rules="versionRules"
        size="mini"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="versionForm.name" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-radio-group v-model="versionForm.type">
            <el-radio label="Java">Java</el-radio>
            <el-radio label="Go">Go</el-radio>
            <el-radio label="Maven">Maven</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="installPath">
          <el-input
            v-model="versionForm.installPath"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input
            v-model="versionForm.description"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" v-if="versionForm.type == 'Maven'">
          <div v-for="(item, index) in repositories" :key="index">
            <el-row>
              <el-col :span="22">
                <el-form
                  size="mini"
                  label-width="80px"
                  label-position="left"
                  class="form-box"
                >
                  <el-form-item label="ID">
                    <el-input
                      v-model="item.repositoryId"
                      placeholder="ID"
                    />
                  </el-form-item>
                  <el-form-item label="">
                    <el-input
                      v-model="item.repositoryUrl"
                      placeholder="maven"
                    />
                  </el-form-item>
                  <el-form-item label="">
                    <el-input
                      v-model="item.userName"
                      placeholder=""
                    />
                  </el-form-item>
                  <el-form-item label="">
                    <el-input
                      v-model="item.password"
                      placeholder="maven"
                    />
                  </el-form-item> </el-form
              ></el-col>
              <el-col :span="2"
                ><i
                  @click="deleteItem(index)"
                  class="el-icon-delete-solid delete-icon"
              /></el-col>
            </el-row>
          </div>
          <div class="add-text" @click="addRepository"></div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitVersion('versionForm')"
            ></el-button
          >
          <el-button type="info" @click="cancelVersion"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import systemApi from '../../http/System'
export default {
  data() {
    return {
      systemForm: {
        gitType: 'Gitlab',
        secrets: [],
      },
      repoForm: {},
      showCreateSecret: false,
      mavenForm: {},
      configType: 'git',
      versionData: [],
      searchVersion: '',
      showSecret: true,
      showVersionDialog: false,
      versionForm: {},
      versionRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' },
        ],
        type: [
          { required: true, message: '', trigger: 'change' },
        ],
        installPath: [
          { required: true, message: '', trigger: 'blur' },
          { max: 100, message: '100', trigger: 'blur' },
        ],
      },
      consoleVersion: '',
      editTool: false,
      repositories: [],
      secretForm: {},
      confirmRefresh: false,
    }
  },
  methods: {
    refreshSecret() {
      this.confirmRefresh = false
      systemApi.createSecret().then((res) => {
        if (res.data) {
          this.showSecret = false
          this.$set(this.systemForm, 'pushSecret', res.data)
          this.$notify.success('')
        }
      })
    },
    deleteItem(index) {
      this.repositories.splice(index, 1)
    },
    addRepository() {
      this.repositories.push({})
    },
    cancelVersion() {
      this.showVersionDialog = false
      this.repositories = []
      this.editTool = false
    },
    handleEdit(row) {
      this.editTool = true
      this.showVersionDialog = true
      this.versionForm = row
      if (row.buildConfig) {
        this.repositories = JSON.parse(row.buildConfig)
      }
    },
    handleDelete(row) {
      this.$alert(
        '',
        '',
        {
          confirmButtonText: '',
          callback: (action) => {
            if (action != 'confirm') {
              return
            }
            systemApi.deleteTool(row.toolId).then((res) => {
              if (res.data) {
                this.$notify.success('')
                this.getVersionList()
              } else {
                this.$notify.error('')
              }
            })
          },
        }
      )
    },
    startAddVersion() {
      this.showVersionDialog = true
      this.versionForm = {}
    },
    submitVersion(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        this.versionForm.buildConfig = JSON.stringify(this.repositories)
        if (!this.editTool) {
          systemApi.createTool(this.versionForm).then((res) => {
            if (res.data) {
              this.$notify.success('')
              this.cancelVersion()
              this.getVersionList()
            } else {
              this.$notify.error('')
            }
          })
        } else {
          systemApi.updateTool(this.versionForm).then((res) => {
            if (res.data) {
              this.$notify.success('')
              this.cancelVersion()
              this.getVersionList()
            } else {
              this.$notify.error('')
            }
          })
        }
      })
    },
    getVersionList() {
      systemApi.getBuildTools().then((res) => {
        this.versionData = res.data
      })
    },
    filterLanguageTag(value, row) {
      return row.tag === value
    },
    changeGit(type) {
      if (type == 'Gitlab') {
        this.systemForm.owner = 'oauth2'
      }
      if (type != 'Gitlab' && this.systemForm.owner == 'oauth2') {
        this.systemForm.owner = ''
      }
    },
    getGitConfig() {
      systemApi.requestGitConfig().then((res) => {
        this.systemForm = res.data
        if (!this.systemForm.secrets) {
          this.systemForm.secrets = []
        }
      })
    },
    getRepoConfig() {
      systemApi.getImageRepository().then((res) => {
        this.repoForm = res.data
      })
    },
    getMavenConfig() {
      systemApi.requestMavenConfig().then((res) => {
        this.mavenForm = res.data
      })
    },
    submitMaven() {
      systemApi.updateMavenConfig(this.mavenForm).then((res) => {
        if (res.data) {
          this.$notify.success('Maven')
        } else {
          this.$notify.error('Maven')
        }
      })
    },
    submitGit() {
      systemApi.updateGitConfig(this.systemForm).then((res) => {
        if (res.data) {
          this.$notify.success('Git')
        } else {
          this.$notify.error('Git')
        }
      })
    },
    submitImage() {
      systemApi.updateRepository(this.repoForm).then((res) => {
        if (res.data) {
          this.$notify.success('')
        } else {
          this.$notify.error('')
        }
      })
    },
    clickTab() {
      if (this.configType == 'git') {
        this.getGitConfig()
      }

      if (this.configType == 'repo') {
        this.getRepoConfig()
      }

      if (this.configType == 'maven') {
        this.getMavenConfig()
      }
      if (this.configType == 'build') {
        this.getVersionList()
      }
    },
    getSystemVersion() {
      systemApi.getSystemVersion().then((res) => {
        this.consoleVersion = res.data.consoleVersion
      })
    },
  },
  created() {
    this.getSystemVersion()
    this.getGitConfig()
  },
}
</script>
<style lang="less" scoped>
.tips-info {
  width: 100%;
}
.content {
  width: 70%;
  padding: 10px;
}
.version-box {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;

  color: #c0c4cc;
  text-align: center;
  font-size: 12px;
}
.form-box {
  border-left: 4px solid #409eff;
  padding-left: 5px;
}
.delete-icon {
  margin-left: 20px !important;
  cursor: pointer;
}
.add-text {
  height: 30px;
  line-height: 30px;
  text-align: center;
  vertical-align: middle;
  border: 1px dashed #c0c4cc;
  margin-top: 20px;
  width: 200px;
  margin-left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
}
</style>
