<template>
  <div class="content">
    <!--  -->
    <el-form
      :inline="true"
      :model="queryForm"
      size="mini"
      @submit.native.prevent
    >
      <el-form-item label="">
        <el-input
          @input="startQuery"
          clearable
          v-model="queryForm.name"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select
          v-model="serviceId"
          size="small"
          @change="chooseService"
          placeholder=""
        >
          <el-option
            v-for="(item, index) in serviceList"
            :key="index"
            :label="item.serviceName"
            :value="item.serviceId"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="startQuery"
          ></el-button
        >
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="startCreate"
          ></el-button
        >
      </el-form-item>
    </el-form>
    <!--  -->

    <el-tabs v-model="activeName" @tab-click="selectTab">
      <el-tab-pane label="" name="first">
        <!--  -->
        <el-table :data="taskData" size="mini" style="width: 100%">
          <el-table-column prop="taskId" label="Id"> </el-table-column>
          <el-table-column prop="taskName" label=""> </el-table-column>
          <el-table-column prop="description" label="">
            <template slot-scope="scope">
              <i
                v-if="scope.row.isRunning"
                class="el-icon-loading loading-status"
              />
              <span v-else>--</span>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="">
            <template slot-scope="scope">
              {{ scope.row.createTime | dateFormat }}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                icon="el-icon-video-play"
                @click="startRun(scope.row)"
                plain
                ></el-button
              >
              <el-button size="mini" @click="editTask(scope.row)" plain
                ></el-button
              >
              <el-button
                size="mini"
                type="danger"
                plain
                @click="deleteTask(scope.row)"
                ></el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <div>
          <el-pagination
            :page-size="10"
            :current-page="taskCurrentPage"
            @current-change="taskPageChange"
            layout="prev, pager, next"
            :total="taskTotal"
          >
          </el-pagination>
        </div>
        <!--  -->
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <!--  -->
        <el-table :data="recordData" size="mini" style="width: 100%">
          <el-table-column prop="taskName" label=""> </el-table-column>
          <el-table-column prop="statusName" label="">
            <template slot-scope="scope">
              <el-tag :type="scope.row.status | statusFormat">{{
                scope.row.statusName
              }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="percent" label="">
            <template slot-scope="scope">
              {{ scope.row.percent ? scope.row.percent + '%' : '-' }}
            </template>
          </el-table-column>
          <el-table-column prop="executeUser" label=""> </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span> {{ scope.row.updateTime | dateFormat }}</span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot="header">
              <el-button
                size="mini"
                type="primary"
                icon="el-icon-refresh"
                @click="getTaskRecords(1)"
                ></el-button
              >
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                v-if="scope.row.status == 4"
                icon="el-icon-video-pause"
                @click="stopRun(scope.row)"
                plain
                ></el-button
              >
              <el-button size="mini" @click="showRecord(scope.row)" plain
                ></el-button
              >
              <el-button
                size="mini"
                plain
                type="danger"
                @click="deleteTaskRecord(scope.row)"
                ></el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div>
          <el-pagination
            :page-size="10"
            :current-page="recordCurrentPage"
            @current-change="recordPageChange"
            layout="prev, pager, next"
            :total="recordTotal"
          >
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>

    <!--  -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="showDialog"
      @close="closeDialog"
      width="70%"
    >
      <el-form
        :model="taskForm"
        ref="taskForm"
        :rules="taskRule"
        size="small"
        label-width="120px"
      >
        <el-form-item label="" prop="taskName">
          <el-input
            v-model="taskForm.taskName"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input
            v-model="taskForm.description"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="caseType" @change="chooseType">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2">e2e</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" v-if="caseType == 1" prop="serviceId">
          <el-select
            v-model="taskForm.serviceId"
            @change="selectService"
            placeholder=""
          >
            <el-option
              v-for="item in serviceList"
              :key="item.serviceId"
              :label="item.serviceName"
              :value="item.serviceId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="testCaseId">
          <el-select
            v-model="taskForm.testCaseId"
            @change="selectTestCase"
            placeholder=""
          >
            <el-option
              v-for="item in testCases"
              :key="item.testCaseId"
              :label="item.testCaseName"
              :value="item.testCaseId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-button
            type="primary"
            icon="el-icon-refresh"
            size="mini"
            class="sync-btn"
            @click="refreshConfig"
            ></el-button
          >
          <codeeditor
            ref="editer"
            :codes="jsonStr"
            @change="dataChange"
            :readonly="false"
          ></codeeditor>
        </el-form-item>

        <!-- <el-form-item label="">
          <div style="text-align: center">
            <el-transfer
              style="text-align: left; display: inline-block"
              v-model="executeList"
              filterable
              :render-content="renderFunc"
              :titles="['', '']"
              :button-texts="['', '']"
              @change="handleChange"
              :format="{
                noChecked: '${total}',
                hasChecked: '${checked}/${total}',
              }"
              :data="machineList"
            >
            </el-transfer>
          </div>
        </el-form-item> -->

        <el-form-item>
          <el-button type="primary" @click="submit('taskForm')"></el-button>

          <el-button type="info" @click="closeDialog"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
  </div>
</template>
<script>
import codeeditor from '../../components/CodeEditor.vue'
import serviceApi from '../../http/Service'
import testCaseApi from '../../http/TestCase'
import taskApi from '../../http/Task'
export default {
  components: { codeeditor },
  data() {
    return {
      recordData: [],
      taskData: [],
      queryForm: {},
      taskForm: {},
      showDialog: false,
      dialogTitle: '',
      isEdit: false,
      activeName: 'first',
      machineList: [],
      executeList: [],
      testCases: [],
      jsonStr: ``,
      taskCurrentPage: 1,
      taskTotal: 0,
      recordCurrentPage: 1,
      recordTotal: 0,
      caseType: 1,
      taskRule: {
        taskName: [
          { required: true, message: '', trigger: 'blur' },
        ],
        serviceId: [
          { required: true, message: '', trigger: 'select' },
        ],
        testCaseId: [
          { required: true, message: '', trigger: 'select' },
        ],
      },
      serviceList: [],
      serviceId: '',
    }
  },
  methods: {
    chooseService() {
      this.getTasks(1, this.queryForm.name)
    },
    getServices() {
      this.serviceList = []
      serviceApi.getServices().then((res) => {
        this.serviceList = res.data
      })
    },
    chooseType(type) {
      if (type == 2) {
        this.testCases = []
        testCaseApi.getE2EList().then((res) => {
          this.testCases = res.data
        })
      }
    },
    refreshConfig() {
      this.selectTestCase(this.taskForm.testCaseId)
    },
    dataChange(info) {
      this.jsonStr = info
    },
    selectService(service) {
      this.testCases = []
      testCaseApi.getTestCaseList(service, 1, 100).then((res) => {
        this.testCases = res.data.data
      })
    },
    selectTestCase(testCaseId) {
      let array = {}
      testCaseApi.getTestCaseConfigs(testCaseId).then((res) => {
        if (res.data) {
          res.data.forEach((element) => {
            array[element.paramKey] = this.exchangeType(
              element.paramType,
              element.value
            )
          })

          this.jsonStr = JSON.stringify(array, null, 3)
        }
      })
    },
    exchangeType(paramType, value) {
      switch (paramType) {
        case 'String':
          return value
        case 'List':
          return JSON.parse(value)
        case 'Map':
          return JSON.parse(value)
        case 'Integer':
          return parseInt(value)
        case 'Float':
          return parseFloat(value)
        case 'Double':
          return parseFloat(value)
      }
      return value
    },
    startRun(row) {
      taskApi.runTask(row.taskId).then((res) => {
        if (res.data) {
          this.$notify.success(',')
        } else {
          this.$notify.error('')
        }
      })
      this.getTasks(this.taskCurrentPage, this.queryForm.name)
    },
    stopRun(row) {
      taskApi.stopTask(row.recordId).then(() => {
        this.$notify.success(',')
      })
      this.getTasks(this.taskCurrentPage, this.queryForm.name)
      this.getTaskRecords(this.recordCurrentPage)
    },
    startQuery() {
      this.getTasks(1, this.queryForm.name)
    },
    startCreate() {
      this.showDialog = true
      this.taskForm = {}
      this.isEdit = false
    },
    editTask(row) {
      this.isEdit = true
      this.taskForm = row
      this.showDialog = true
      this.selectService(row.serviceId)
      this.jsonStr = JSON.stringify(JSON.parse(row.taskConfig), null, 3)
      if (row.machines) {
        this.executeList = JSON.parse(row.machines)
      }
    },
    deleteTask(row) {
      this.$confirm(`${row.taskName}?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        taskApi.deleteTask(row.taskId).then((res) => {
          if (res.data) {
            this.$notify.success('')
            this.getTasks(this.taskCurrentPage, this.queryForm.name)
          } else {
            this.$notify.error('')
          }
        })
      })
    },
    closeDialog() {
      this.showDialog = false
      this.taskForm = {}
    },
    submit(taskForm) {
      this.$refs[taskForm].validate((valid) => {
        if (!valid) {
          return false
        }

        this.taskForm.machines = JSON.stringify(this.executeList)
        this.taskForm.taskConfig = this.jsonStr
        if (this.isEdit) {
          taskApi.updateTask(this.taskForm).then((res) => {
            this.closeDialog()
            this.getTasks(1)
            if (res.data == 1) {
              this.$notify.success('')
              return
            }

            this.$message.warning('')
          })
          return
        }

        taskApi.createTask(this.taskForm).then((res) => {
          this.closeDialog()
          this.getTasks(1)
          if (res.data == 1) {
            this.$notify.success('')
            return
          }

          this.$message.warning('')
        })
      })
    },
    getTasks(page, name) {
      if (!name) {
        name = ''
      }
      taskApi.getTaskList(page, 10, name, this.serviceId).then((res) => {
        this.taskTotal = res.data.total
        this.taskCurrentPage = 1
        this.taskData = res.data.data
      })
    },
    getTaskRecords(page) {
      taskApi.getTaskRecords(page, 10).then((res) => {
        this.recordTotal = res.data.total
        this.recordCurrentPage = page
        this.recordData = res.data.data
      })
    },
    selectTab() {
      if (this.activeName == 'first') {
        this.getTasks(1, this.queryForm.name)
      }

      if (this.activeName == 'second') {
        this.getTaskRecords(1)
      }
    },
    taskPageChange(page) {
      this.getTasks(page, this.queryForm.name)
    },
    recordPageChange(page) {
      this.getTaskRecords(page)
    },
    showRecord(row) {
      this.$router.push({
        path: '/record/detail',
        query: {
          recordId: row.recordId,
        },
      })
    },
    deleteTaskRecord(row) {
      taskApi.deleteTaskRecord(row.recordId).then(() => {
        this.$notify.success('')
        this.getTaskRecords(this.recordCurrentPage)
      })
    },
  },
  created() {
    this.getServices()
    this.getTasks(1, '')
  },
}
</script>
<style scoped>
.content {
  margin: 5px;
}
.header-line {
  text-align: center;
}
.delete-icon {
  font-size: 20px;
  cursor: pointer;
}
.op-icon {
  margin-left: 10px;
}
.loading-status {
  font-size: 20px;
}
.sync-btn {
  margin: 10px 10px;
}
</style>
