<template>
  <div>
    <el-divider content-position="left"></el-divider>
    <el-form-item label="POST">
      <el-radio-group v-model="dataForm.bodyType">
        <el-radio label="json">json</el-radio>
        <el-radio label="form-data">form-data</el-radio>
        <el-radio label="x-www-form-urlencoded">x-www-form-urlencoded</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="">
      <el-input
        placeholder="Action"
        v-model="dataForm.actionUrl"
        @input="notifyParam"
      />
    </el-form-item>
    <el-form-item label="">
      <el-row
        v-for="(item, index) in paramList"
        :key="index"
        class="config-line"
      >
        <el-col :span="5">
          <el-input
            placeholder=""
            v-model="item.name"
            size="mini"
            @input="notifyParam"
          />
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="6">
          <el-input
            placeholder=""
            v-model="item.description"
            @input="notifyParam"
          />
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="4">
          <el-select size="mini" v-model="item.position" placeholder="">
            <el-option
              v-for="item in positionList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="5">
          <el-input
            placeholder=""
            size="mini"
            v-model="item.value"
            @input="notifyParam"
          />
        </el-col>
        <el-col :span="1" class="delete-icon"
          ><i class="el-icon-remove-outline" @click="deleteParam(index)"
        /></el-col>
      </el-row>
      <div class="add-btn" @click="addNewItem"></div>
    </el-form-item>

    <el-divider content-position="left"></el-divider>
    <el-form-item label="">
      <el-input
        placeholder=""
        size="mini"
        v-model="dataForm.queryUrl"
        @input="notifyParam"
      />
    </el-form-item>
    <el-form-item>
      <template slot="label">
        
        <el-tooltip placement="top">
          <div slot="content">
            windy,<br />
            <br />(1)
          </div>
          <i class="el-icon-question tip" />
        </el-tooltip>
      </template>
      <el-row>
        <el-col :span="4">
          <el-input
            size="mini"
            placeholder="Key"
            v-model="loopQuery.compareKey"
            @input="notifyParam"
          />
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="4">
          <el-select
            size="mini"
            v-model="loopQuery.valueType"
            placeholder=""
            @change="notifyParam"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="4">
          <el-select
            size="mini"
            v-model="loopQuery.operator"
            placeholder=""
            @change="notifyParam"
          >
            <el-option
              v-for="item in operators"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="4">
          <el-input
            size="mini"
            placeholder=""
            v-model="loopQuery.value"
            @input="notifyParam"
          />
        </el-col>
      </el-row>
    </el-form-item>
    <el-form-item label="">
      <template slot="label">
        
        <el-tooltip placement="top">
          <div slot="content">
            <br />:Keyresult,
            1 jsonresult1
          </div>
          <i class="el-icon-question tip" />
        </el-tooltip>
      </template>
      <el-row
        v-for="(item, index) in compareList"
        :key="index"
        class="config-line"
      >
        <el-col :span="4">
          <el-input
            placeholder="Key"
            v-model="item.compareKey"
            @input="notifyParam"
          />
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="4">
          <el-input
            placeholder=""
            v-model="item.description"
            @input="notifyParam"
          />
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="4">
          <el-select
            v-model="item.valueType"
            placeholder=""
            @change="notifyParam"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="3">
          <el-select
            v-model="item.operator"
            placeholder=""
            @change="notifyParam"
          >
            <el-option
              v-for="item in operators"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="1" class="separate-line">-</el-col>
        <el-col :span="4">
          <el-input
            placeholder=""
            v-model="item.value"
            @input="notifyParam"
          />
        </el-col>
        <el-col :span="1" class="delete-icon"
          ><i class="el-icon-remove-outline" @click="deleteCompare(index)"
        /></el-col>
      </el-row>
      <div class="add-btn" @click="addNewCondition"></div>
    </el-form-item>
  </div>
</template>
<script>
import Template from '../../feature/template.vue'
export default {
  props: {
    form: Object,
    Template,
  },
  watch: {
    form: {
      handler(val) {
        this.dataForm = val
        if (val.paramList && val.paramList.length > 0) {
          this.paramList = val.paramList
        }
        if (val.compareResults && val.compareResults.length > 0) {
          this.compareList = val.compareResults
        }
        this.notifyParam()
      },
      deep: true,
      immediate: true,
    },
  },
  data() {
    return {
      loopQuery: {},
      dataForm: {},
      paramList: [{ position: 'Body' }],
      compareList: [{ showCompare: true }],
      operators: [
        { label: '()', value: 'equal' },
        { label: '', value: '>' },
        { label: '', value: '>=' },
        { label: '', value: '=' },
        { label: '', value: '<' },
        { label: '', value: '<=' },
      ],
      typeList: [
        { label: '', value: 'Integer' },
        { label: '', value: 'String' },
      ],
      positionList: [
        { label: 'Body', value: 'Body' },
        { label: 'Path', value: 'Path' },
        { label: 'Query', value: 'Query' },
        { label: 'Header', value: 'Header' },
      ],
    }
  },
  methods: {
    deleteCompare(index) {
      if (this.compareList.length == 1) {
        return
      }
      this.compareList.splice(index, 1)
    },
    deleteParam(index) {
      this.paramList.splice(index, 1)
    },
    notifyParam() {
      this.$emit('notifyParam', {
        paramList: this.paramList,
        compareList: this.compareList,
        actionUrl: this.dataForm.actionUrl,
        queryUrl: this.dataForm.queryUrl,
      })
    },
    changeBodyType(value) {
      if (value == 'none') {
        this.paramList = []
      } else {
        if (this.paramList.length < 1) {
          this.paramList.push({ position: 'Body' })
        }
      }
    },
    addNewItem() {
      this.paramList.push({ position: 'Body' })
    },
    addNewCondition() {
      this.compareList.push({ showCompare: true })
    },
  },
  destroyed() {
    this.paramList = []
  },
  created() {
    this.dataForm = this.form
    this.notifyParam()
  },
}
</script>
<style scoped>
.separate-line {
  text-align: center;
}
.delete-icon {
  color: #f56c6c;
  padding-left: 10px;
  cursor: pointer;
}
.add-btn {
  height: 30px;
  line-height: 30px;
  text-align: center;
  vertical-align: middle;
  border: 1px dashed #ccc;
  margin: 10px 20%;
  cursor: pointer;
}
.config-line {
  margin: 0 0 10px 0px;
}
.tip {
  cursor: pointer;
}
</style>
